var slider = require('./modules/slider.js'),
  reviews = require('./modules/reviews.js'),
  third = require('./modules/third.js');

if (/\/company|\/index/i.test(window.location.pathname)) {
  slider.init();
  reviews.init();
}

if (/contacts/i.test(window.location.pathname)) {
  third.init();
  third.warn();
}