var init = function() {
  var inputs = document.querySelectorAll('#input');

  inputs.forEach(function(item) {
    item.addEventListener('focus', focused);
    item.addEventListener('blur', blured);
  });

  function focused(e) {
    var ePlaceholder = this.parentNode.querySelector('.feedback-placeholder');
    ePlaceholder.style.display = 'block';
  }

  function blured(e) {
    var ePlaceholder = this.parentNode.querySelector('.feedback-placeholder');
    if (!e.target.value) {
      ePlaceholder.style.display = 'none';
    }
  }


};

module.exports = {
  init: init
};