var reviews = function() {
  var reviewsCounter = 0;
  var clickButton = document.getElementsByClassName('reviews-control__button')
      clickButtonLength = clickButton.length;

  for (var i = 0; i < clickButtonLength; i++) {
    clickButton[i].onclick = function(e) {
      var slideNum = e.target.innerHTML,
          sliderItems = document.getElementsByClassName('reviews-control__button'),
          sliderItemsLength = sliderItems.length;

      for ( var i=0; i<sliderItemsLength; i++ ) {
        sliderItems[i].classList.remove('active-review');
      }
      e.target.classList.add('active-review');
      moveSlide(slideNum);
    };
  }

  function moveSlide(e) {
    var sliderList = document.getElementsByClassName('reviews-list'),
        slideNum = e,
        leftValue = -slideNum*100 + '%';
    
    sliderList[0].style.left = leftValue;
  };
};

module.exports = {
  init: reviews
};