var slider = function() {
  var counter = 0;
  var clickButton = document.getElementsByClassName('slider-controls__button'),
      clickButtonLength = clickButton.length;

  var intervalOnSlider = setInterval(function() {
    moveSlide(1);
  }, 10000);

  for (var i = 0; i < clickButtonLength; i++) {
    clickButton[i].onclick = function(e) {
      var button = e.target,
        buttonAttribute = button.getAttribute('class');

      // ~buttonAttribute.indexOf, 
      // ~buttonAttribute.lastIndexOf раньше использовал, работает одинаково
      // ~ для получения false, при отсутствии слова indexOf и lastIndexOf возвращают -1
      // что при ~-1 === 1
      // в классе элемента должно быть искомое слово чтобы сработал сдвиг
      // через регулярное выражение .test работает красивее, на выходе true/false
      // не надо ничего преобразовывать
      if ( /right/i.test(buttonAttribute) ) {
        moveSlide(1);
        clearInterval(intervalOnSlider);
      }

      if ( /left/i.test(buttonAttribute) ) {
        moveSlide(0);
        clearInterval(intervalOnSlider);
      }
    };
  }

  function moveSlide(e) {
    var sliderList = document.getElementsByClassName('slider-list'),
      sliderItems = document.getElementsByClassName('slider-item'),
      sliderLength = sliderItems.length,
      leftValue;

    if (e) {
      counter++;
      if (counter > sliderLength-1) {
        counter = 0;
      }
      leftValue = -counter*100 + '%';
      sliderList[0].style.left = leftValue;
      // sliderList[0] - потому getElementsByClassName возвращает array
    } else {
      counter--;
      if (counter < 0) {
        counter = sliderLength-1;
      }
      leftValue = -counter*100 + '%';
      sliderList[0].style.left = leftValue;
    }
  }
};

module.exports = {
  init: slider
};
